<template>
  <div class="homePage-wrap">
    <div class="ul-wrap">
      <ul>
        <li v-for="(item,index) in blogList" style="margin-bottom: 50px;">
          <div class="title">{{item.article_title}}</div>
          <div class="content" v-html="item.article_text"></div>
          <div class="btn-wrap"><el-button @click="readAll(item.id)" size="mini">阅读全文</el-button></div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
  export default {
    name: "homePage",
    data(){
      return{
        blogList:[],
        loginInfo:JSON.parse(this.$cookie.get('loginInfo'))||''
      }
    },
    mounted(){
      this.getBlogList()
    },
    methods:{
      getBlogList(){
        this.$http.get('/article/getArticle?u_id='+this.loginInfo.u_id).then((res) => {
          if(res && res.code == '200'){
            console.log(res)
            this.blogList = res.data
          }else{
            this.$message(res.msg);
          }
        })
      },
      readAll(params){
        this.$router.push({ path: `/blogDetail/${params}` })
      }
    }
  }
</script>

<style lang="stylus">
.homePage-wrap
  width 1200px
  margin 0 auto
  padding 20px
  .ul-wrap
    ul
      li
        padding 30px
        .title
          font-size 18px
          line-height 24px
        .content
          margin-top 10px
          padding-left 40px
          font-size 12px
          line-height 18px
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        .btn-wrap
          text-align center
</style>



