<template>
  <div class="register-vue">
    <div class="form-wrap">
      <el-input  placeholder="请输入用户名" v-model="userName" type="text"></el-input>
      <el-input  placeholder="请输入密码"  v-model="password" type="password"></el-input>
      <el-input  placeholder="请再次输入密码"  v-model="secondPassword" type="password"></el-input>
      <el-button @click="regBtn" type="primary">注册</el-button>
      <div class="register-wrap">
        已有账号？<a href="javascript:;" class="toRegister" @click="toLogin">立即登录</a>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "register",
    data(){
      return{
        userName:'',
        password:'',
        secondPassword:'',
      }
    },
    methods:{
      regBtn(){
        if(this.userName == ''){
          this.$message('请输入用户名！');
          return
        }
        if(this.password == ''){
          this.$message('请输入密码！');
          return
        }
        if(this.password != this.secondPassword){
          this.$message('请确认两次密码输入一致！');
          return
        }
        this.$http.post('/users/register',{
          userName:this.userName,
          password:this.password
        }).then((response) => {
          console.log(response)
          if(response.data.code != '200'){
            this.$message(response.data.msg);
          }else{
            this.$message({
              message:'注册成功！',
              type:'success'});
          }
        })
      },
      toLogin(){
        this.$router.push('/login')
      }
    }
  }
</script>

<style scoped lang="stylus">
  .register-vue
    width 100%
    height 100%
    background url('../assets/images/login-bg.png') no-repeat;
    background-size cover
    background-color #b8e5f8
    padding-top 100px
    .form-wrap
      width 400px
      height 350px
      margin 0px auto
      background #fff
      padding 10px 30px
      .el-input
        margin-top 20px
      .forgetP-wrap
        height 30px
        text-align right
        line-height 42px
        .forget-btn
          color #666
          font-size 14px
      .el-button
        margin-top 20px
        width 100%
      .register-wrap
        margin-top 40px
        text-align center
        color #999
        font-size 14px
        .toRegister
          color #69b3fc
    .jjj
      width 10px
</style>



