<template>
  <div class="writeBlog-wrap">
    <el-form ref="form" :model="form" label-width="80px">
      <el-form-item label="标题">
        <el-input v-model="form.name"></el-input>
      </el-form-item>
      <el-form-item label="标签">
        <el-select v-model="form.tag" placeholder="标签">
          <el-option :label="item.name" :value="item.id" :key="item.id" v-for="(item,index) in tagList"></el-option>
          <!--<el-option label="区域二" value="beijing"></el-option>-->
        </el-select>
        <el-button @click="addTag">添加标签</el-button>
      </el-form-item>
    </el-form>
    内容：<uEditor @getContent="getContent" ref="uEditor"></uEditor>
    <el-button @click="publish">发布</el-button>
    <el-button @click="test">测试</el-button>
  </div>
</template>

<script>
  import uEditor from '../components/uEditor'
  export default {
    name: "writeBlog",
    components:{
      uEditor
    },
    data(){
      return {
        title:'',
        content:'',
        loginInfo:JSON.parse(this.$cookie.get('loginInfo'))||'',
        editorOption:{},
        tagList:[],
        form:{
          name:'',
          tag:''
        },
      }
    },
    mounted(){
      this.getTag()
    },
    methods:{
      onEditorBlur(){//失去焦点事件
      },
      onEditorFocus(){//获得焦点事件
      },
      onEditorChange(){//内容改变事件
      },
      getTag(){
        this.$http.get('/users/getTag?u_id='+this.loginInfo.u_id).then((response) => {
          if(response.code != '200'){
            this.$message(response.data.msg);
          }else{
            this.tagList = response.data
          }
        })
      },
      saveTag(value){
        let params = {
          name:value,
          u_id:this.loginInfo.u_id
        }
        this.$http.post('/article/saveArticleTag',params).then((res)=>{
          if(res.code != '200'){
            this.$message(res.data.msg);
          }else{
            this.$message({
              message: '添加成功',
              type: 'success'
            });
            this.getTag()
          }
        })
      },
      addTag(){
        this.$prompt('请输入标签名称', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          //inputPattern: /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/,
          //inputErrorMessage: '邮箱格式不正确'
        }).then(({ value }) => {
          this.saveTag(value)
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '取消输入'
          });
        });
      },
      test(){
        this.$refs.uEditor.test()
      },
      getContent(value){
        this.content = value
      },
      publish() {
        this.$refs.uEditor.test()
        let params = {
          u_id:this.loginInfo.u_id,
          article_title:this.form.name,
          article_tag_id:this.form.tag,
          article_content:this.content.msg,
          article_text:this.content.text
        }
        this.$http.post('/users/publish',params).then((response) => {
          if(response.code == '200'){
            this.$message({
              message: '发布成功',
              type: 'success'
            });
            this.$router.push('/')
          }else{
            this.$message(response.data.msg);
          }
        })
      }
    }
  }
</script>

<style scoped>

</style>



