<template>
  <div class="blogDetail-wrap">
    <div class="title">{{blogData.article_title}}</div>
    <div class="content" v-html="blogData.article_content"></div>
  </div>
</template>

<script>
  export default {
    name: "blogDetail",
    data(){
      return{
        blogData:{}
      }
    },
    mounted(){
      this.getBlogData()
    },
    methods:{
      getBlogData(){
        this.$http.get('/article/getArticleById?id='+this.$route.params.id).then((res) => {
          if(res && res.code == '200'){
            console.log(res)
            this.blogData = res.data
          }else{
            this.$message(res.msg);
          }
        })
      },
    }
  }
</script>

<style lang="stylus">
.blogDetail-wrap
  width 1000px
  margin:0 auto
  .title
    font-size 20px
    line-height 30px
    text-align center
    color #333
</style>



