<template>
  <div class="header-wrap">
    <div class="header-content">
      <!--<a href="javascript:;" class="userInfo">登录</a>-->
      <!--<el-dropdown>
        <el-button type="primary">
          {{loginInfo.userName}}<i class="el-icon-arrow-down el-icon&#45;&#45;right"></i>
        </el-button>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item>个人中心</el-dropdown-item>
          <el-dropdown-item>退出</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>-->
      <router-link to="/">首页</router-link>
      <router-link to="/writeBlog">写博客</router-link>
      <el-dropdown>
        <el-button type="primary" size="small">
          {{loginInfo.userName}}<i class="el-icon-arrow-down el-icon--right"></i>
        </el-button>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item>个人中心</el-dropdown-item>
          <el-dropdown-item @click.native="loginOut">退出</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>

  </div>
</template>

<script>
  export default {
    name: "headerC",
    data(){
      return {
        loginInfo:JSON.parse(this.$cookie.get('loginInfo'))||''
      }
    },
    methods:{
      loginOut(){
        this.$cookie.remove('loginInfo')
        this.$router.push('/login')
      }
    }
  }
</script>

<style scoped lang="stylus">
.header-wrap
  width 100%
  height 40px
  line-height 40px
  background #f1f1f1
  .header-content
    width 1000px
    margin 0 auto
    .userInfo
      float right
      color #333
  .el-dropdown
    float right
  .jjj
    width 20px
</style>



