<template>
  <div class="login-vue">
    <div class="form-wrap">
      <el-input  placeholder="请输入用户名" v-model="userName" type="text"></el-input>
      <el-input  placeholder="请输入密码"  v-model="password" type="password"></el-input>
      <div class="forgetP-wrap">
        <a href="javascript:;" class="forget-btn">忘记密码？</a>
      </div>
      <el-button @click="loginBtn" type="primary">登录</el-button>
      <div class="register-wrap">
        没有账号？<a href="javascript:;" class="toRegister" @click="toRegister">立即注册</a>
      </div>
    </div>
  </div>
</template>

<script>
  import {mapMutations} from 'vuex'
  export default {
    name: "login",
    data(){
      return {
        userName:'',
        password:'',
        listArr:[]
      }
    },
    methods:{
      ...mapMutations(['setToken']),
      loginBtn(){
        if(this.userName == ''){
          this.$message('请输入用户名！');
          return
        }
        if(this.password == ''){
          this.$message('请输入密码！');
          return
        }
        this.$http.post('/users/login',{
          userName:this.userName,
          password:this.password
        }).then((response)=>{
          console.log(response)
          this.listArr = response.data
          if(response.code == '200'){
            this.$message({message:'登录成功',type:'success'});
            localStorage.setItem('token', response.data.token);
            this.setToken(response.data.token)
            this.$cookie.set('loginInfo', {userName:this.userName, password:this.password,u_id:response.data.u_id}, { path: '' });
            this.$router.push('/index')
          }else{
            this.$message(response.msg);
          }
        })
      },
      toRegister(){
        this.$router.push('/register')
      }
    }
  }
</script>

<style lang="stylus">
.login-vue
  width 100%
  height 100%
  background url('../assets/images/login-bg.png') no-repeat;
  background-size cover
  background-color #b8e5f8
  padding-top 100px
  .form-wrap
    width 320px
    height 330px
    margin 0px auto
    background #fff
    padding 10px 30px
    .el-input
      margin-top 20px
    .forgetP-wrap
      height 30px
      text-align right
      line-height 42px
      .forget-btn
        color #666
        font-size 14px
    .el-button
      margin-top 20px
      width 100%
    .register-wrap
      margin-top 40px
      text-align center
      color #999
      font-size 14px
      .toRegister
        color #69b3fc
  .jjj
    width 10px
</style>



