import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import jsCookie from 'js-cookie'



import login from '@/views/login'
import register from '@/views/register'
import index from '@/views/index'
import writeBlog from '@/views/writeBlog'
import homePage from '@/views/homePage'
import blogDetail from '@/views/blogDetail'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path:'/',
      redirect:'/homePage'
    },
    {
      path: '/',
      beforeEnter: (to, from, next) => {
        if(jsCookie.get('loginInfo')){
          next()
        }else{
          console.log('weidenglu')
          next({path:"/login"})
        }
      },
      name: 'index',
      component: index,
      children:[{
        path:'/writeBlog',
        name:'writeBlog',
        component:writeBlog
      },{
        path:'/homePage',
        name:'homePage',
        component:homePage
      },{
        path:'/blogDetail/:id',
        name:'blogDetail',
        component:blogDetail
      }]
    },{
      path:'/login',
      name:'login',
      component: login
    },{
      path:'/register',
      name:'register',
      component: register
    },{
      path:'*',
      redirect: "/"
    }
  ]
})



